/* Dark theme with subtle gradient background */
body,
html {
  margin: 0;
  padding: 0;
  background: linear-gradient(135deg, #1f2937, #111827);
  color: #e0e7ff;
  font-family: Arial, sans-serif;
  min-height: 100vh;
}

.banner {
  position: relative;
  height: 100px;
  width: 100%;
  background-image: url("https://via.placeholder.com/1920x100"); /* Replace with your image */
  background-size: cover;
  background-position: center;
}

.overlay-box {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* semi-transparent overlay */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
}

.overlay-box h1 {
  margin: 0;
  font-size: 24px;
}

.overlay-box p {
  margin: 4px 0 0 0;
  font-size: 16px;
}

/* Sticky header bar */
.sticky-header {
  position: sticky;
  top: 0;
  background: linear-gradient(90deg, #3b82f6, #2563eb);
  padding: 12px 20px;
  z-index: 1000;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.7);
}

.header-container {
  max-width: 800px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  font-weight: bold;
}

.logo {
  font-size: 1.25rem;
}

/* Contact button in header */
.contact-button {
  background: transparent;
  border: 2px solid white;
  color: white;
  padding: 6px 16px;
  border-radius: 6px;
  font-size: 1rem;
  cursor: pointer;
  transition:
    background-color 0.3s,
    color 0.3s;
}

.contact-button:hover {
  background-color: white;
  color: #2563eb;
}

/* Main landing page styles */
.landing-page {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px 20px;
}

.main-header {
  text-align: center;
  margin-bottom: 60px;
}

.main-header h1 {
  font-size: 2.8rem;
  margin-bottom: 20px;
  color: #e0e7ff;
}

.main-header p {
  font-size: 1.2rem;
  color: #cbd5e1;
  margin-bottom: 30px;
}

.enroll-button {
  padding: 12px 28px;
  font-size: 1rem;
  background-color: #3b82f6;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.enroll-button:hover {
  background-color: #2563eb;
}

.section {
  margin-bottom: 50px;
}

.section h2 {
  font-size: 1.8rem;
  color: #f3f4f6;
  margin-bottom: 12px;
}

.section p {
  font-size: 1.05rem;
  color: #cbd5e1;
  line-height: 1.6;
}
.image-section {
  text-align: center;
  margin-bottom: 40px;
}

.hero-image {
  width: 100%;
  height: auto;
  max-height: 400px; /* You can adjust this to your liking */
  object-fit: cover;
  display: block;
  margin: 0 auto;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.modal-content {
  background: white;
  padding: 2rem;
  border-radius: 10px;
  max-width: 500px;
  width: 90%;
  text-align: center;
  position: relative;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 1.5rem;
  background: none;
  border: none;
  cursor: pointer;
}

.image-section {
  position: relative;
  width: 100%;
  height: auto;
}

.hero-image {
  width: 100%;
  height: auto;
  display: block;
}

.hero-overlay {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  color: white;
  background: rgba(0, 0, 0, 0.5);
  padding: 2rem;
  border-radius: 10px;
  max-width: 80%;
}

.hero-overlay h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.hero-overlay p {
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
}

.enroll-button {
  background-color: #ff6600;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
}
